






















import Vue from 'vue';
import NumberPad from '@/components/Money/NumberPad.vue';
import FormItem from '@/components/Money/FormItem.vue';
import Tags from '@/components/Money/Tags.vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import Title from '@/components/Money/Title.vue';
import {RecordItem} from '@/custom';
import DatePicker from '@/components/Money/DatePicker.vue';
// import Notes from '@/components/Money/Notes.vue';

@Component({
  components: {Tabs, Tags, FormItem, NumberPad, Title, DatePicker},
})
export default class Money extends Vue {
  recordTypeList = recordTypeList;
  record: RecordItem = this.initRecord();

  initRecord(): RecordItem {
    return {
      category: {name: '一般', type: 'expense', svg: '一般', id: ''},
      notes: '',
      type: 'expense',
      amount: 0,
      createdAt: new Date().toISOString()
    };
  }

  confirm() {
    const _this= this;
    if (this.record.amount === 0) {
      this.$confirm({
        content: '金额为0，确定吗？',
        okText: '确定',
        cancelText: "取消",
        centered:true,
        onOk(){
          _this.$store.commit('createRecord',_this.record)
          _this.$message.success({content: '已保存',duration:1});
        }
      })
    } else {
      this.$store.commit('createRecord', this.record);
      this.$message.success({content: '已保存',duration:1});
    }
    this.record.notes = ''
    this.record.amount = 0
  }
}
