


















































import Vue from 'vue';
import moment from 'moment';
import {Component, Watch} from 'vue-property-decorator';
import ChooseMonth from '@/components/Home/ChooseMonth.vue';
import clone from '@/lib/clone';
import EmptyData from '@/components/Money/EmptyData.vue';
import {RecordItem, RootState} from '@/custom';
import _ from 'lodash';

type HashTableValue = { title: string, item: RecordItem[] }
type hashTable = { [key: string]: HashTableValue }

@Component({
  components: {EmptyData, ChooseMonth},
  filters: {
    addComma(amount: number) {
      if (!amount) {return;}
      return amount.toLocaleString();
    }
  }
})
export default class Home extends Vue {
  month = '';
  expense = 0;
  income = 0;
  clonedList = clone(this.recordList).map(r => _.pick(r, ['createdAt', 'amount', 'category', 'type']));

  @Watch('month')
  updateBalance() {
    let thisMonthList = [];
    for (let i = 0; i < this.clonedList.length; i++) {
      if (this.clonedList[i].createdAt!.indexOf(this.month) > -1) {
        thisMonthList.push(this.clonedList[i]);
      }
    }
    const totalMap = new Map();
    for (let i = 0; i < thisMonthList.length; i++) {
      const type: string = thisMonthList[i].type;
      const value: number = thisMonthList[i].amount;
      if (totalMap.has(type)) {
        const prevValue = totalMap.get(type);
        const currentValue = prevValue + value;
        totalMap.set(type, currentValue);
      } else {
        totalMap.set(type, value);
      }
    }
    if (totalMap.get('income')) {
      this.income = totalMap.get('income');
    } else {
      this.income = 0;
    }
    if (totalMap.get('expense')) {
      this.expense = totalMap.get('expense');
    } else {
      this.expense = 0;
    }
    return this.expense & this.income;
  }

  comment() {
    if (this.income - this.expense > 0) {
      this.$success({
        centered: true,
        title: '攒钱高手',
        content: '这个月会发财哦！'
      });
    } else if ((this.income) && (this.income - this.expense <= 0)) {
      this.$warning({
        centered: true,
        title: '入不敷出',
        content: '注意收支平衡哦！'
      });
    }
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get result() {
    const {recordList} = this;
    const totalTable: hashTable = {};
    //声明这个hashTable的key是字符串，value是HashTableValue类型
    const newList = clone(recordList).sort((a, b) => moment(b.createdAt).valueOf() - moment(a.createdAt).valueOf());
    for (let i = 0; i < newList.length; i++) {
      const date = newList[i].createdAt!;
      if (date.indexOf(this.month) > -1) {
        totalTable[date] = totalTable[date] || {title: date, item: []};
        totalTable[date].item.push(newList[i]);
      }
    }
    return totalTable;
  }

  showTotal(group: HashTableValue) {
    let total = 0;
    let item: RecordItem;
    for (item of group.item) {
      if (item.type === 'expense') {
        total -= item.amount;
      } else {
        total += item.amount;
      }
    }
    if (total > 0) {
      return '+' + this.addComma(total);
    } else if (total < 0) {
      return '-' + this.addComma(total);
    } else {
      return '0';
    }
  }

  addComma(amount: number) {
    return amount.toString().replace(/\D/g, '')
        .replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }

  showNote(item: RecordItem) {
    if (item.notes !== '') {
      return '(' + item.notes + ')';
    }
  }

  showAmount(item: RecordItem) {
    let commaAmount = this.addComma(item.amount);
    if (item.type === 'income') {
      return '+' + commaAmount;
    } else {
      return '-' + commaAmount;
    }
  }
}
