





















import Vue from 'vue';
import Icon from '@/components/Icon.vue';
import {Component, Prop, Watch} from 'vue-property-decorator';
import {Tag} from '@/custom';

@Component({
  components: {Icon}
})
export default class Tags extends Vue {
  @Prop() selectedTag!: Tag;
  @Prop() type!: string;

  created() {
    this.$store.commit('fetchTags');
  }

  get filteredList() {
    return this.$store.state.tagList.filter((tag: Tag) => tag.type === this.type)
  }

  @Watch('type')
  updateCategory() {
    if (this.type === '+') {
      this.select({name: '美妆', type: 'income', id: '', svg: ''});
    } else {
      this.select({name: '一般', type: 'expense', id: '', svg: ''});
    }
  }

  select(tag: Tag) {
    this.$emit('update:selectedTag', tag);
  }

  edit() {
    this.$router.push('/money/labels');
  }
}
